<template>
  <section class="app-container">
    <header class="header flex baseline flex-end">
      <!-- <a>
        <dict-helper title="logOut" v-bind:display-lang="userDefinedLang"></dict-helper>
      </a>-->
      <div class="action-btns">
        <a href="https://pamoda.dhamma.org/he/contact-us-2/" target="_blank">
          <dict-helper title="contact" v-bind:display-lang="userDefinedLang"></dict-helper>
        </a>
        <button class="change-lang" @click="changeLang()">
          <dict-helper title="langButton" v-bind:display-lang="userDefinedLang"></dict-helper>
        </button>
      </div>
    </header>

    <article class="form-container">
      <h3 class="page-title">
        <dict-helper title="pageTitle" v-bind:display-lang="userDefinedLang"></dict-helper>
      </h3>
      <section v-if="studentDetails.firstName">
        <h3>
          <dict-helper
            title="welcome"
            v-bind:display-lang="userDefinedLang"
            aria-live="polite"
          >{{studentDetails.firstName + ' ' + studentDetails.lastName}}</dict-helper>
        </h3>
      </section>

      <div v-if="studentDetails.firstName">
        <div>
          <h3>
            <dict-helper
              v-if="courseType !== ''"
              v-bind:title="courseType"
              v-bind:display-lang="userDefinedLang"
            ></dict-helper>
          </h3>
          <h3>
            <dict-helper title="locationTitle" v-bind:display-lang="userDefinedLang"></dict-helper>
          </h3>
          <h3>{{courseDates}}</h3>
          <h3 v-if="studentDetails.status !== 5">
            <dict-helper title="actionQuestion" v-bind:display-lang="userDefinedLang"></dict-helper>
          </h3>
          <confirm-radio-btn
            v-on:choose-action="sendAction($event)"
            v-bind:isConfirmed="studentDetails.confirmed"
            v-bind:status="studentDetails.status"
            v-bind:lang="userDefinedLang"
          ></confirm-radio-btn>
        </div>
      </div>

      <div v-else>
        <!-- If the form is not found or the link was broken -->
        <p>
          <dict-helper title="noStudentData" v-bind:display-lang="userDefinedLang"></dict-helper>
        </p>
        <h5>
          <dict-helper title="centerName" v-bind:display-lang="userDefinedLang"></dict-helper>
        </h5>
      </div>
    </article>
    <button class="reset" @click="reset">Reset</button>
  </section>
</template>

<script>
import dictHelper from "../components/DictHelper";
import confirmRadioBtn from "../components/ConfirmRadioBtn.vue";
// import formService from "../services/formService.js";

export default {
  name: "Home",
  components: {
    confirmRadioBtn,
    dictHelper
  },
  data() {
    return {
      token: this.$route.query.token,
      studentData: null,
      studentDetails: {
        knowHebrew: "en"
      },
      courseType: "",
      userChangedLang: false,
      userDefinedLang: "en"
    };
  },
  created() {
    this.studentData = {
      studentDetails: {
        firstName: "נוף",
        lastName: "לבני",
        confirmed: false,
        gender: "נ",
        status: 1,
        email: "idodidodi@gmail.com",
        knowHebrew: "he"
      },
      courseType: "tn"
    };
    if (this.studentData) {
      this.studentDetails = this.studentData.studentDetails;
      this.courseType = this.studentData.courseType;
    } else {
      this.studentDetails = { knowHebrew: "en" };
      this.courseType = "";
    }
    this.userDefinedLang = this.displayLang;
  },
  methods: {
    sendAction(response) {
      this.studentDetails.confirmed = response === "Confirm" ? true : false; // Set confirmed prop to true or false.
      if (response === "Cancel") this.studentDetails.status = 5; // No. 5 is cancellation status
    },
    reset() {
      location.reload();
    },
    changeLang() {
      this.userChangedLang = !this.userChangedLang;
      switch (this.userChangedLang) {
        case true:
          this.userDefinedLang =
            this.defaultLang === "en" ? this.hebType : "en";
          break;
        case false:
          this.userDefinedLang = this.displayLang;
      }
    }
  },
  computed: {
    fromDate: function() {
      return "10.07.2021";
    },
    toDate: function() {
      return "21.07.2021";
    },
    courseDates: function() {
      return `${this.fromDate} - ${this.toDate}`;
    },
    defaultLang: function() {
      const lang = this.studentDetails ? this.studentDetails.knowHebrew : "en";
      let hebOptions = ["עב", "עיב", "he", "hib"];
      for (let option of hebOptions) {
        if (lang.includes(option)) {
          return "heb";
        }
      }
      return "en";
    },
    displayLang: function() {
      switch (this.defaultLang) {
        case "en":
          return "en";
        case "heb":
          return this.hebType;
        default:
          return "en";
      }
    },
    hebType: function() {
      return this.studentDetails.gender === "נ" ? "hebF" : "hebM";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.reset {
  position: fixed;
  bottom: 0px;
  background-color: pink !important;
  left: 0px;
  cursor: pointer;
}
.app-container {
  position: relative;
  margin-left: 30vw;

  .action-btns {
    z-index: 1;
    cursor: pointer;
  }
}
// .page-title {
//   flex-basis: 80%;
// }
</style>
