<template>
  <p>
    {{text}} 
    <slot></slot>
  </p>
</template>

<script>
import { dict } from "../services/dictionaryService";

export default {
  props: {
    title: String,
    displayLang: String,
    defaultText: String
  },
  computed: {
    text: function() {
      return dict[this.title][this.displayLang];
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  p {
    display: inline;
  }
</style>