<template>
  <section>
    <h4 v-if="confirmed === false && isCancelled === false">
      <input v-if="lang === 'en'" type="radio" name="action" value="Confirm" v-model="picked" />
      <dict-helper title="confirm" v-bind:display-lang="lang"></dict-helper>
      <input v-if="lang !== 'en'" type="radio" name="action" value="Confirm" v-model="picked" />
    </h4>
    <h4 v-else-if="isCancelled === false">
      <dict-helper title="confirmationReceived" v-bind:display-lang="lang"></dict-helper>
    </h4>
    <h4 v-else>
      <dict-helper title="cancellationReceived" v-bind:display-lang="lang"></dict-helper>
    </h4>
    <h4 v-if="isCancelled === false">
      <input v-if="lang === 'en'" type="radio" name="action" value="Cancel" v-model="picked" />
      <dict-helper title="cancel" v-bind:display-lang="lang"></dict-helper>
      <input v-if="lang !== 'en'" type="radio" name="action" value="Cancel" v-model="picked" />
      <h4>
        <button v-on:click="submit">
          <dict-helper title="submit" v-bind:display-lang="lang"></dict-helper>
        </button>
      </h4>
    </h4>
  </section>
</template>

<script>
import dictHelper from "../components/DictHelper";

export default {
  props: {
    isConfirmed: Boolean,
    status: Number,
    lang: String
  },
  components: {
    dictHelper
  },
  data() {
    return {
      picked: null
    };
  },

  methods: {
    submit() {
      if (this.picked === null)
        alert("Pick one of the options and then click Submit");
      // To do - change to custom alert with countdown from 3 to 0 timer
      else {
        this.$emit("choose-action", this.picked);
        this.picked = null;
      }
    }
  },
  computed: {
    confirmed: function() {
      return this.isConfirmed;
    },
    isCancelled: function() {
      return this.status === 5 ? true : false;
    }
  }
};
</script>

